<template>
  <div id="app">
    <div class="wssearch" >
      <search-input


      ></search-input>
      <selected-pils
   

      ></selected-pils>
      <div 
        v-if="aggs" 
        class="wssearch--filters"
      >
        <date-range
          v-if="showdaterange"

         ></date-range>
          <component 
            :is="filtertypeconfig.includes(name) ? FacetCombobox : FacetFilter"
            v-for="(name, index) in aggstoshow"
            :key="index"
            :buckets="aggs[name].buckets"
            :name="name"
          ></component>
      </div>
      <div
        class="wssearch--total"
        >
        {{$i18n('search-total', total)}}
      </div>
      <div 
        class="wssearch--main" 
        :class="mainloading"
      >
          <div class="wssearch--hits">
            <hit-item
              v-for="(hit, i) in hits"
              :hit="hit"
              :key="i"
            ></hit-item>
          </div>
          <hit-pagers
            :size="size"
            :from="from"
            :total="total"
          ></hit-pagers>
      </div>
    </div>
  </div>
</template>

<script>
import store from './store.js'
import mw from './mock.js'

import SearchInput from './components/SearchInput.vue'
import PillsSelected from './components/PillsSelected.vue'
import FacetDateRange from './components/FacetDateRange.vue'
import HitItem from './components/HitItem.vue'
import HitPagers from './components/HitPagers.vue'
import FacetCombobox from './components/FacetCombobox.vue'
import FacetFilter from './components/FacetFilter.vue'


export default {
  store: store,
  name: 'App',
  components: {
    'search-input':SearchInput,
    'selected-pils': PillsSelected,
    'date-range': FacetDateRange,
    'hit-item': HitItem,
    'hit-pagers': HitPagers
  },
  mounted(){
     this.$store.commit('START', true)
  },
  data(){
    return {
      FacetCombobox: FacetCombobox,
      FacetFilter: FacetFilter,
      facetconfig: mw.config.values.WSSearchFront.facetSettings
    }
  },
  computed:{
    aggstoshow(){
      return this.facetconfig.date ? Object.keys(this.aggs).sort() : Object.keys(this.aggs).sort().filter(el => el != 'Date')
    },
    showdaterange(){
      return this.facetconfig.datepicker ? true : false
    },
    filtertypeconfig(){
      return this.facetconfig.combobox ? this.facetconfig.combobox : []
    },
    mainloading(){
      if(this.$store.state.loading){
        return 'wssearch--main__loading';
      }
      return false;
    },
    hits(){
       return this.$store.state.hits
    },
    aggs(){
      return this.$store.state.aggs
    },
    total(){
      return this.$store.state.total
    },
    from(){
      return this.$store.state.from
    },
    size(){
       return this.$store.state.size
    }    
  }
};
</script>

<style>
.wssearch--filters {
  grid-area: filters;
}
  .wssearch--filter {
    position: relative;
  }
  .wssearch--total {
    grid-area: total;
    font-weight: bold;
}
