.wssearch {
  display: grid;
  grid-template-columns: 20ch 1fr;
  grid-template-rows:    auto
                         minmax(2em, auto)
                         auto
                         1fr;
  grid-template-areas:'search search'
                      'selected selected'
                      'filters total'
                      'filters main';
  grid-gap: .5em 2em;
}

.wssearch--main {
  grid-area: main;
  transition: filter .2s ease-in-out;
}
.wssearch--main__loading{
  filter: blur(1px);
}

.wssearch--filter-header{
  font-weight: bold;
}
#app{
    font-size: 0.875em;
    font-size: calc(1em * 0.875);
    line-height: 1.6;
    font-family: sans-serif;
}

